import React, { useState, useEffect } from "react";
import {
  Card,
  CardBody,
  CardHeader,
  Col,
  Row,
  CardFooter,
  Button,
  Input,
  FormGroup,
  Label,
  Table,
} from "reactstrap";
import "react-toastify/dist/ReactToastify.css";
import { toast, ToastContainer } from "react-toastify";
import axios from "axios";
import Spinner from "reactstrap/lib/Spinner";
import Classes from "./product.module.css";

const Seller = (props) => {
  const [mainCategoryFromServer, setMainCategoryFromServer] = useState([]);
  const [allSellers, setAllSellers] = useState([]);
  const [values, setValues] = useState({
    title: "",
    label: "",
  });
  const [submitLoading, setSubmitLoading] = useState(false);
  const [catId, setCatId] = useState("");

  const handleState = (e) => {
    const { name, value } = e.target;
    setValues((prev) => ({ ...prev, [name]: value }));
  };

  useEffect(() => {
    //all category
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        query getAllCategory($page:Int,$limit:Int,$mainCategory:Boolean,$hasParent:Boolean,$catId:ID) {
          getAllCategory(input:{page:$page,limit:$limit,mainCategory:$mainCategory,hasParent:$hasParent,catId:$catId}){
            _id,
            name
          }
        }`,
        variables: {
          page: 1,
          limit: 30,
          mainCategory: true,
          hasParent: false,
          catId: null,
        },
      },
    })
      .then((res) => {
        const { getAllCategory } = res.data.data;
        setMainCategoryFromServer(getAllCategory);
      })
      .catch((err) => console.log(err));
  }, []);

  const handleSubmit = (e) => {
    if (values.title === "") {
      toast.warn("فیلد عنوان فروشنده محصول را باید وارد نمائید");
      return false;
    }

    setSubmitLoading(true);

    axios({
      url: "/",
      method: "post",
      data: {
        query: `
            mutation addSeller($category:ID!,$name:String!,$label:String){
                seller(input:{name:$name,label:$label,category:$category}){
                  status,
                  message
                }
              }
            `,
        variables: {
          name: values.title,
          label: values.label,
          category: catId,
        },
      },
    })
      .then((res) => {
        setSubmitLoading(false);
        if (res.data.errors) {
          const { message } = res.data.errors[0].data[0];
          toast.error(message);
        } else {
          const { message, _id } = res.data.data.seller;
          toast.success(message);
          setAllSellers((prev) => [
            ...prev,
            { _id: _id, name: values.title, label: values.label, flag: false },
          ]);
          setValues({ title: "", label: "" });
        }
      })
      .catch((err) => console.log(err));
  };

  const handleMainCat = (e) => {
    setCatId(e.target.value);
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
          query getAllSeller($catId:ID!){
              getAllSellers(catId:$catId){
                _id,
                name,
                label,
              }
            }
              `,
        variables: {
          catId: e.target.value,
        },
      },
    })
      .then((res) => {
        const { getAllSellers } = res.data.data;
        getAllSellers.map((item) => (item.flag = false));
        setAllSellers(getAllSellers);
      })
      .catch((err) => console.log(err));
  };

  const handleEdit = (e, id) => {
    const copy = [...allSellers];
    const res = copy.filter((item) => item._id === id);
    res[0].flag = true;
    setAllSellers(copy);
  };

  const changeValueName = (e, id) => {
    const copy = [...allSellers];
    const res = copy.filter((item) => item._id === id);
    res[0].name = e.target.value;
    setAllSellers(copy);
  };

  const changeValueLabel = (e, id) => {
    const copy = [...allSellers];
    const res = copy.filter((item) => item._id === id);
    res[0].label = e.target.value;
    setAllSellers(copy);
  };

  const handleSubmitUpdate = (e, id) => {
    const copy = [...allSellers];
    const result = copy.filter((item) => item._id === id);
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
            mutation UpdateSelelrs($name:String!,$label:String,$sellerId:ID!){
                updateSeller(sellerId:$sellerId,name:$name,label:$label){
                  status,
                  message
                }
              }
            `,
        variables: {
          name: result[0].name,
          label: result[0].label,
          sellerId: id,
        },
      },
    })
      .then((res) => {
        setSubmitLoading(false);
        if (res.data.errors) {
          const { message } = res.data.errors[0].data[0];
          toast.error(message);
        } else {
          const { message } = res.data.data.updateSeller;
          toast.success(message);
          result[0].flag = false;
          setAllSellers(copy);
        }
      })
      .catch((err) => console.log(err));
  };


  return (
    <div className="animated fadeIn">
      <ToastContainer />
      <Row>
        <Col xs="12">
          <Card>
            <CardHeader>اضافه کردن فروشنده محصول </CardHeader>
            <CardBody>
              <FormGroup row>
                <Col xs="3">
                  <FormGroup>
                    <Label htmlFor="Maincategory">دسته اصلی</Label>
                    <Input
                      type="select"
                      name="Maincategory"
                      id="Maincategory"
                      onChange={handleMainCat}
                    >
                      <option></option>
                      {mainCategoryFromServer.map((item, index) => (
                        <option key={item._id} value={item._id}>
                          {item.name}
                        </option>
                      ))}
                    </Input>
                  </FormGroup>
                </Col>
                <Col xs="3">
                  <FormGroup>
                    <Label htmlFor="title">عنوان فروشنده محصول</Label>
                    <Input
                      type="text"
                      placeholder="عنوان فروشنده محصول را وارد نمایید"
                      name="title"
                      value={values.title}
                      onChange={(e) => handleState(e)}
                      required
                    />
                  </FormGroup>
                </Col>
                <Col xs="3">
                  <FormGroup>
                    <Label htmlFor="label">توضیح فروشنده محصول</Label>
                    <Input
                      type="text"
                      placeholder="توضیحات فروشنده محصول را وارد نمایید"
                      name="label"
                      value={values.label}
                      onChange={(e) => handleState(e)}
                      required
                    />
                  </FormGroup>
                </Col>{" "}
              </FormGroup>
            </CardBody>
            <CardFooter>
              <Button
                type="submit"
                color="primary"
                size="sm"
                onClick={handleSubmit}
              >
                {submitLoading ? <Spinner size="sm" /> : "ثبت فروشنده محصول"}
              </Button>
            </CardFooter>
          </Card>
        </Col>
      </Row>

      <Row>
        <Col>
          <Card>
            <CardHeader>
              <Row>
                <Col xs="2">
                  <i className="fa fa-align-justify"></i>
                  <span style={{ marginRight: "5px" }}>همه فروشنده ها</span>
                </Col>
              </Row>
            </CardHeader>
            <CardBody>
              <Table striped responsive size="sm">
                <thead>
                  <tr>
                    <th>عنوان فروشنده محصول</th>
                    <th>توضیحات فروشنده محصول</th>
                    <th>عملیات</th>
                  </tr>
                </thead>
                <tbody>
                  {allSellers.map((item, index) => (
                    <tr key={item._id}>
                      <td>
                        {item.flag ? (
                          <Input
                            type="text"
                            name="title"
                            value={item.name}
                            onChange={(e) => changeValueName(e, item._id)}
                          />
                        ) : (
                          item.name
                        )}
                      </td>
                      <td>
                        {" "}
                        {item.flag ? (
                          <Input
                            type="text"
                            name="label"
                            value={item.label}
                            onChange={(e) => changeValueLabel(e, item._id)}
                          />
                        ) : (
                          item.label
                        )}
                      </td>
                      <td>
                        <div className={Classes.BOX}>
                          {item.flag ? (
                            <span
                              className={Classes.editRemove}
                              onClick={(e) => handleSubmitUpdate(e, item._id)}
                            >
                              <i
                                className="fa fa-check-square-o fa-lg mt-4"
                                style={{
                                  position: "absolute",
                                  top: "-9px",
                                  left: "-4px",
                                }}
                              ></i>
                            </span>
                          ) : (
                            <span
                              className={Classes.edit}
                              onClick={(e) => handleEdit(e, item._id)}
                            >
                              ویرایش
                            </span>
                          )}

                          <span className={Classes.delete}>حذف</span>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </Table>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default Seller;
